import { Day } from 'date-fns';
import firebase from 'firebase';
import GeoPoint = firebase.firestore.GeoPoint;
import {classToPlain, Exclude, plainToClass, Type} from 'class-transformer';

export class Market {
  private _id?: string;
  private _name: string;
  private _description: string;
  private _picture?: string;
  private _marketDay: Day;
  private _excludedDays: Date[];
  private _isActive: boolean;

  @Exclude({ toClassOnly: true })
  private _location: GeoPoint;

  private _hourStart: Date;
  private _hourFinish: Date;

  constructor() {
    this._excludedDays = [];
    this.name = 'Mercat Agroecologic UPV';
    this.description = 'Test';
    this.marketDay = 4;
    this.addExcludedDays(new Date(2021, 4, 29));
    this.isActive = true;
    this.location = new GeoPoint(39.48151487502771, -0.343266787040054);
    this.hourStart = new Date(2021, 4, 5, 9, 0);
    this.hourFinish = new Date(2021, 4, 5, 13, 30);
  }

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get name(): string {
    return this._name;
  }

  set name(value: string) {
    this._name = value;
  }

  get description(): string {
    return this._description;
  }

  set description(value: string) {
    this._description = value;
  }

  get picture(): string {
    return this._picture;
  }

  set picture(value: string) {
    this._picture = value;
  }

  get marketDay(): Day {
    return this._marketDay;
  }

  set marketDay(value: Day) {
    this._marketDay = value;
  }

  get excludedDays(): Date[] {
    return this._excludedDays;
  }

  set excludedDays(value: Date[]) {
    this._excludedDays = value;
  }

  addExcludedDays(value: Date) {
    if (this.excludedDays != null){
      this.excludedDays.push(value);
    } else {
      this.excludedDays = [value];
    }
  }

  get isActive(): boolean {
    return this._isActive;
  }

  set isActive(value: boolean) {
    this._isActive = value;
  }

  get location(): firebase.firestore.GeoPoint {
    return this._location;
  }

  set location(value: firebase.firestore.GeoPoint) {
    this._location = value;
  }

  getLocationUrl(){
    return 'https://www.google.com/maps/search/?api=1&query='
      + this.location.latitude + ','
      + this.location.longitude;
  }

  get hourStart(): Date {
    return this._hourStart;
  }

  set hourStart(value: Date) {
    this._hourStart = value;
  }

  get hourFinish(): Date {
    return this._hourFinish;
  }

  set hourFinish(value: Date) {
    this._hourFinish = value;
  }

  convertToDTO() {
    const objectEntity = classToPlain(this);
    objectEntity._location = this.location;
    return objectEntity;
  }

  convertFromDTO(marketEntity) {
    const marketModel = plainToClass(Market, marketEntity);
    marketModel.location = marketEntity._location;
    marketModel.excludedDays = [];
    marketEntity._excludedDays.forEach(day => {
      marketModel.excludedDays.push(new Date(day.seconds * 1000));
    });
    marketModel.hourFinish = new Date(marketEntity._hourFinish.seconds * 1000);
    marketModel.hourStart = new Date(marketEntity._hourStart.seconds * 1000);
    return marketModel;
  }
}
