import {Component, Input, OnInit} from '@angular/core';
import {ModalController, ViewWillEnter} from '@ionic/angular';
import {User} from '../../models/user/user.model';
import {ChatsService} from '../../services/chat/chats.service';
import {Chat} from '../../models/chat/chat';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ChatMessage} from '../../models/chat/message/chat-message';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {ProfilesService} from '../../services/profiles/profiles.service';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.view.html',
  styleUrls: ['./chat.view.scss']
})
export class ChatView implements OnInit, ViewWillEnter {
  @Input() id: string;

  public me: User;
  public other: User;

  public currentChat: Chat;
  public messageForm: FormGroup;

  constructor(public modalController: ModalController, public chatService: ChatsService, private fb: FormBuilder,
              public authService: AuthServiceService, public profileService: ProfilesService) {
    this.currentChat = new Chat();

    this.messageForm = this.fb.group({
      message: ['', [Validators.required]]
    });
  }

  ionViewWillEnter(): void {
    this.chatService.getChatById(this.id).then(value => {
      this.currentChat = value;
      const currentUserId = this.authService.getCurrentUserId();
      if (currentUserId === this.currentChat.members[0]){
        this.profileService.loadUserById(this.currentChat.members[0]).then(user => {
          this.me = user;
        });
        this.profileService.loadUserById(this.currentChat.members[1]).then(user => {
          this.other = user;
        });
      } else {
        this.profileService.loadUserById(this.currentChat.members[0]).then(user => {
          this.other = user;
        });
        this.profileService.loadUserById(this.currentChat.members[1]).then(user => {
          this.me = user;
        });
      }
    });
  }

  ngOnInit(): void {
  }

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalController.dismiss({
      dismissed: true
    });
  }

  sendMessage() {
    if (this.messageForm.valid) {
      const chatMessage = new ChatMessage();
      chatMessage.message = this.messageForm.value.message;
      chatMessage.ownerId = this.authService.getCurrentUserId();
      this.chatService.addMessageToChat(chatMessage, this.id);
    }
  }

  isMessageMine(ownerId: string) {
    return ownerId === this.me.id;
  }

  getMessageOwner(ownerId: string) {
    return ownerId === this.me.id ? this.me : this.other;
  }
}
