import { Injectable } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Chat} from '../../models/chat/chat';
import {Market} from '../../models/market/market';
import {Router} from '@angular/router';
import {User} from '../../models/user/user.model';
import {ProductOrder} from '../../models/cart/product-order';
import {plainToClass} from 'class-transformer';
import {ModalController} from '@ionic/angular';
import {ProfileModifyModalComponent} from '../../components/profile-modify-modal/profile-modify-modal.component';
import {ChatView} from '../../views/chat/chat.view';
import {ChatMessage} from '../../models/chat/message/chat-message';

@Injectable({
  providedIn: 'root'
})
export class ChatsService {
  private chatsCollection: AngularFirestoreCollection<unknown>;

  constructor(public firestore: AngularFirestore, private router: Router, public modalCtrl: ModalController) {
    this.chatsCollection = this.firestore.collection('chats');
  }

  createChatOrOpen(ownerId: string, userId) {
    return new Promise<any>((resolve, reject) => {
      this.getAllUserChats(ownerId).then(chatList => {
        const commonChat = chatList.filter(chat => chat.members.includes(userId));

        // Check if the chat exists
        if (commonChat.length !== 0){
          // Navigate to chat
          resolve(this.openChatModal(commonChat[0].id));
        } else {
          // Create chat
          const newChat = new Chat();
          newChat.members.push(ownerId, userId);

          this.createChat(newChat).then(id => {
            resolve(this.openChatModal(id));
          });
        }
      });
    });
  }

  createChat(chat: Chat) {
    return new Promise<any>((resolve, reject) => {
      this.chatsCollection.add(chat.convertToDTO()).then(value => {
        resolve(value.id);
      });
    });
  }

  getAllUserChats(ownerId: string): Promise<Chat[]> {
    return new Promise<any>((resolve, reject) => {
      this.firestore.collection('chats',
        ref => ref.where('_members', 'array-contains', ownerId)).valueChanges({idField: '_id'})
        .subscribe(chatListEntity => {
          const chatList: Chat[] = [];
          chatListEntity.forEach(chatEntity => {
            chatList.push(new Chat().convertFromDTO(chatEntity));
          });
          resolve(chatList);
        });
    });
  }

  openChatModal(chatId: string) {
    this.modalCtrl.create({
      component: ChatView,
      componentProps: {
        id: chatId,
      }
    }).then(value => {
      value.present().then(r => {
        return true;
      });
    });
  }

  getChatById(chatId: string) {
    return new Promise<any>((resolve, reject) => {
      this.chatsCollection.doc(chatId).valueChanges({idField: '_id'}).subscribe(value => {
        const chat = new Chat().convertFromDTO(value);
        this.chatsCollection.doc(chatId).collection('messages').valueChanges().subscribe(messages => {
          const messagesList: ChatMessage[] = [];
          messages.forEach((chatMessage, index) => {
            messagesList.push(new ChatMessage().convertFromDTO(chatMessage));
          });
          messagesList.sort((a, b) => a.creationDate.getTime() - b.creationDate.getTime());
          chat.messages = messagesList;
        });
        resolve(chat);
      });
    });
  }

  addMessageToChat(chatMessage: ChatMessage, chatId: string){
    return new Promise<any>((resolve, reject) => {
      this.chatsCollection.doc(chatId).collection('messages').add(chatMessage.convertToDTO()).then(value => resolve(true));
    });
  }
}
