import { Component, OnInit } from '@angular/core';
import {MarketService} from '../../services/market/market.service';
import {Market} from '../../models/market/market';
import {nextDay} from 'date-fns/esm';
import {isSameDay} from 'date-fns';
import { add } from 'date-fns';

@Component({
  selector: 'app-pickup-alert',
  templateUrl: './pickup-alert.component.html',
  styleUrls: ['./pickup-alert.component.scss'],
})
export class PickupAlertComponent implements OnInit {
  public marketInfo: Market;
  public nextAvailableDay;

  constructor(public marketService: MarketService) {
    this.marketInfo = new Market();
  }

  ngOnInit() {
    this.marketService.getCurrentMarket().then(value => {
      this.marketInfo = value;

      let today = new Date();
      let nextMarketDay = nextDay(today, this.marketInfo.marketDay);
      let invalidDays = this.marketInfo.excludedDays.filter((day) => {
        return isSameDay(day, nextMarketDay);
      });
      while (invalidDays.length > 0) {
        today = add(today, {weeks: 1});
        nextMarketDay = nextDay(today, this.marketInfo.marketDay);
        invalidDays = this.marketInfo.excludedDays.filter((day) => {
          return isSameDay(day, nextMarketDay);
        });
      }
      this.nextAvailableDay = nextMarketDay;
    });
  }

  openMarketUrl() {
    window.open(this.marketInfo.getLocationUrl(), '_blank');
  }
}
