import {Component, Input, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ModalController} from '@ionic/angular';
import {ProfilesService} from '../../services/profiles/profiles.service';
import {Review} from '../../models/review/review';
import {OrderService} from '../../services/order/order.service';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {User} from '../../models/user/user.model';

@Component({
  selector: 'app-reviewer',
  templateUrl: './reviewer.component.html',
  styleUrls: ['./reviewer.component.scss'],
})
export class ReviewerComponent implements OnInit {
  public reviewForm: FormGroup;

  @Input() orderId: string;
  @Input() ownerId: string;
  @Input() orderUser: User;

  constructor(private fb: FormBuilder, public modalCtrl: ModalController, public authService: AuthServiceService,
              private profileService: ProfilesService, private orderService: OrderService) {
    this.reviewForm = this.fb.group({
      title: ['', [Validators.required]],
      description: [''],
      value: [4],
    });
  }

  ngOnInit() {

  }

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalCtrl.dismiss({
      dismissed: true
    });
  }

  dismissUploaded() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalCtrl.dismiss({
      dismissed: true,
      hasUploaded: true
    });
  }

  enviarReview() {
    const reviewToUpload = new Review();

    reviewToUpload.orderId = this.orderId;
    reviewToUpload.title = this.reviewForm.value.title;
    reviewToUpload.description = this.reviewForm.value.description;
    reviewToUpload.valuation = this.reviewForm.value.value;
    reviewToUpload.reviewerId = this.authService.getCurrentUserId();
    this.orderService.uploadReviewToProfile(this.ownerId, reviewToUpload).then(r => {
      this.dismissUploaded();
    });
  }
}
