import {Component, Input, OnInit} from '@angular/core';
import {ModalController, ToastController, ViewWillEnter} from '@ionic/angular';
import {User} from '../../models/user/user.model';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Camera, CameraResultType, CameraSource} from '@capacitor/core';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {ProfilesService} from '../../services/profiles/profiles.service';

@Component({
  selector: 'app-profile-modify-modal',
  templateUrl: './profile-modify-modal.component.html',
  styleUrls: ['./profile-modify-modal.component.scss'],
})
export class ProfileModifyModalComponent implements OnInit, ViewWillEnter {
  @Input() user: User;
  public updateProfileForm: FormGroup;
  private newUserPicture = '';

  constructor(public modalController: ModalController, private formBuilder: FormBuilder, public authService: AuthServiceService,
              public profileService: ProfilesService, public toastController: ToastController) {
  }

  ngOnInit() {
    this.buildForm();
  }

  ionViewWillEnter(): void {
  }

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  async takePicture() {
    await Camera.getPhoto({
      quality: 50,
      allowEditing: true,
      resultType: CameraResultType.Base64,
      webUseInput: true,
      source: CameraSource.Prompt
    }).then(value => {
      this.updateProfileForm.value.picture = 'Foto cargada';
      this.newUserPicture = value.base64String;
      this.user.picture = 'Foto cargada';
    });
  }

  buildForm() {
    this.updateProfileForm = this.formBuilder.group({
      name: [this.user.name, ],
      phoneNumber: [this.user.phoneNumber, ],
      picture: [this.user.picture, ],
      location: [this.user.location, ],
      description: [this.user.description, ],
    });
  }

  updateProfile() {
    if (this.updateProfileForm.valid) {
      this.fillUserToForm();
      this.profileService.updateUserInfo(this.user, this.newUserPicture).then(value => {
        this.presentToast();
        this.dismiss();
      }).catch(reason => {
        console.log(reason);
        this.presentErrorToast();
      });
    } else {
      return;
    }
  }

  fillUserToForm() {
    this.user.name = this.updateProfileForm.value.name;
    this.user.description = this.updateProfileForm.value.description;
    this.user.phoneNumber = this.updateProfileForm.value.phoneNumber;
    this.user.location = this.updateProfileForm.value.location;
    this.user.description = this.updateProfileForm.value.description;
  }

  async presentToast() {
    const toast = await this.toastController.create({
      message: 'Informacion actualizada.',
      color: 'success',
      duration: 2000
    });
    toast.present();
  }

  async presentErrorToast() {
    const toast = await this.toastController.create({
      message: 'Ha sucedido un error, vuelve a intentarlo.',
      color: 'danger',
      duration: 2000
    });
    toast.present();
  }
}

