import {ProductOrder} from './product-order';
import {Order} from '../order/order';
import {Product} from '../product/product';

export class Cart {
  id: string;
  ordersList: Order[];

  constructor() {
    this.ordersList = [];
  }

  getAllIds() {
    return [];
  }

  getAllItemLength() {
    let orderedItems = 0;
    this.ordersList.forEach(value => {
      orderedItems += value.orderedItems.length;
    });
    return orderedItems;
  }
}
