import { Component, OnInit } from '@angular/core';
import {ModalController, ViewWillEnter} from '@ionic/angular';
import {ChatsService} from '../../services/chat/chats.service';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {Chat} from '../../models/chat/chat';
import {ChatView} from '../chat/chat.view';
import {User} from '../../models/user/user.model';

@Component({
  selector: 'app-chats',
  templateUrl: './chats.view.html',
  styleUrls: ['./chats.view.scss'],
})
export class ChatsView implements OnInit, ViewWillEnter {
  public chatList: Chat[];

  constructor(public modalController: ModalController, public chatService: ChatsService,
              public authService: AuthServiceService, public modalCtrl: ModalController) {}

  ionViewWillEnter(): void {
    this.chatService.getAllUserChats(this.authService.getCurrentUserId()).then(value => {
      this.chatList = value;
    });
  }

  ngOnInit() {}

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalController.dismiss({
      dismissed: true
    });
  }

  openChatModal(chatId: string) {
    this.modalCtrl.create({
      component: ChatView,
      componentProps: {
        id: chatId,
      }
    }).then(value => {
      value.present().then(r => {
        return true;
      });
    });
  }
}
