import {Component, Input, OnInit} from '@angular/core';
import {ProfilesService} from '../../services/profiles/profiles.service';
import {Review} from '../../models/review/review';
import {ModalController} from '@ionic/angular';
import {ReviewListComponent} from '../review-list/review-list.component';

@Component({
  selector: 'app-review-score',
  templateUrl: './review-score.component.html',
  styleUrls: ['./review-score.component.scss'],
})
export class ReviewScoreComponent implements OnInit {
  @Input() ownerId: string;

  public scoreValue = 5;
  public scoreStars = 5;

  public reviewList: Review[];

  constructor(private profileService: ProfilesService, public modalController: ModalController) {
    this.reviewList = [];
  }

  ngOnInit() {
    this.profileService.loadReviewsByOwnerId(this.ownerId).then(value => {
      this.reviewList = value;
      this.getScoreFromReviewList(this.reviewList);
    });
  }

  getScoreFromReviewList(reviewList: Review[]) {
    let totalScore = 0;
    const totalCount = reviewList.length;
    reviewList.forEach(review => {
      totalScore += review.valuation;
    });

    this.scoreValue = (totalScore / totalCount);
    this.scoreStars = Math.round(this.scoreValue * 2) / 2;
  }

  async openReviewListModal() {
    const modal = await this.modalController.create({
      component: ReviewListComponent,
      cssClass: 'my-custom-class',
      componentProps: {
        reviewList: this.reviewList
      }
    });
    return await modal.present();
  }
}
