import {classToPlain, plainToClass} from 'class-transformer';
import {ProductOrder} from '../cart/product-order';
import {User} from '../user/user.model';

export class Review {
  private _id?: string;
  private _title: string;
  private _description?: string;
  private _valuation: number;
  private _creationDate: Date;
  private _orderId: string;
  private _reviewerId: string;
  private _reviewer: User;

  constructor() {
    this._reviewer = new User();
    this.creationDate = new Date();
  }

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get title(): string {
    return this._title;
  }

  set title(value: string) {
    this._title = value;
  }

  get description(): string {
    return this._description;
  }

  set description(value: string) {
    if (value || value.length > 0) {
      this._description = value;
    }
  }

  get valuation(): number {
    return this._valuation;
  }

  set valuation(value: number) {
    this._valuation = value;
  }

  get creationDate(): Date {
    return this._creationDate;
  }

  set creationDate(value: Date) {
    this._creationDate = value;
  }

  get orderId(): string {
    return this._orderId;
  }

  set orderId(value: string) {
    this._orderId = value;
  }

  get reviewerId(): string {
    return this._reviewerId;
  }

  set reviewerId(value: string) {
    this._reviewerId = value;
  }

  get reviewer(): User {
    return this._reviewer;
  }

  set reviewer(value: User) {
    this._reviewer = value;
  }

  convertToDTO() {
    return classToPlain(this);
  }

  convertFromDTO(review) {
    const reviewEntity = plainToClass(Review, review);
    // Se convierte de Timestamp (firebase) a Date la fecha
    reviewEntity.creationDate = review._creationDate.toDate();
    return reviewEntity;
  }
}
