import {classToPlain, plainToClass} from 'class-transformer';

export class ChatMessage {
  private _id?: string;
  private _creationDate: Date;
  private _message: string;
  private _ownerId: string;
  private _isRead: boolean;

  constructor() {
    this.creationDate = new Date();
  }

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get creationDate(): Date {
    return this._creationDate;
  }

  set creationDate(value: Date) {
    this._creationDate = value;
  }

  get message(): string {
    return this._message;
  }

  set message(value: string) {
    this._message = value;
  }

  get ownerId(): string {
    return this._ownerId;
  }

  set ownerId(value: string) {
    this._ownerId = value;
  }

  get isRead(): boolean {
    return this._isRead;
  }

  set isRead(value: boolean) {
    this._isRead = value;
  }

  convertToDTO() {
    return classToPlain(this);
  }

  convertFromDTO(message) {
    const messageEntity = plainToClass(ChatMessage, message);
    // Se convierte de Timestamp (firebase) a Date la fecha
    messageEntity.creationDate = message._creationDate.toDate();
    return messageEntity;
  }
}
