import { Injectable } from '@angular/core';
import {OrderStatusEnum} from '../../models/order/order';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {Market} from '../../models/market/market';

@Injectable({
  providedIn: 'root'
})
export class MarketService {
  private marketCollection: AngularFirestoreDocument<unknown>;

  constructor(public firestore: AngularFirestore) {
    this.marketCollection = this.firestore.collection('config').doc('market');
  }

  getCurrentMarket() {
    return new Promise<any>((resolve, reject) => {
      this.marketCollection.get().subscribe(value => {
        resolve(new Market().convertFromDTO(value.data()));
      });
    });
  }

  generateMarket(market: Market){
    const marketEntity = market.convertToDTO();
    return new Promise<any>((resolve, reject) => {
      this.marketCollection.set(marketEntity).then(value => {
        resolve(true);
      });
    });
  }
}
